import React, { Component, Fragment, useState } from 'react'
import methods from '@utils/method'
// import VConsole from 'vconsole'

if (methods.isMiniprogram) {
    wx.setEnableDebug({
        enableDebug: true
      });
    } else if(/^\d/.test(window.location.hostname) || window.location.hostname==='localhost' || window.location.search.indexOf('debugger=true')!==-1) {
      let VConsole = require('vconsole');
      new VConsole();
}


import './index.scss'

export default function Wraper(props){
    let { title = '', loadend, children, classN='' } = props
    let [isCallback, setIsCallback] = useState(true)
    
    // console.log('prop s', props)
    
    if (methods.isMiniprogram) {
      // 
      wx.setNavigationBarTitle({
        title,
      });
      // wx.showToast({
      //   title,
      // })
      wx.showShareMenu({withShareTicket:true});
    } else {
      document.title = title;
    }

    window.onDealWithNotSupportDom = function(dom) {
        //  dom 
        if (dom.tagName === 'IFRAME') {
            dom.textContent = ' iframe'
        }
    }


    // render
    if (isCallback && Object.prototype.toString.call(loadend) === '[object Function]') {
        setIsCallback(false);
        loadend();

        if (methods.isMiniprogram) {
          // 
          window.addEventListener('wxload', ()=>{
            console.log('wxLoad')
          })
        }else{
          // 
          window.addEventListener('load', ()=>{
            console.log('webLoad')
          })
        }
    }

  return <div className={`wraper ${classN}`}>{children}</div>
}